import React from 'react';

import { RouteConfig } from 'react-router-config';
import { Redirect } from 'react-router-dom';

import ChapterViewer from './views/components/kural/ChapterViewer';
import KuralDetailViewer from './views/components/kural/KuralDetailViewer';
import { KuralListSwipeable } from './views/components/kural/KuralsByChapterViewer';
import LiteratureViewer from './views/components/kural/LiteratureViewer';
import SectionViewer from './views/components/kural/SectionViewer';
import Home from './views/pages/home/home';
import ListPage from './views/pages/list/list';
import Main from './views/pages/main/main';
import Search from './views/pages/search/search';
import SettingsViewer from './views/pages/settings';

export const ROUTE_CONSTANTS = {
  ROOT: '/',
  LIST: '/list',
  SEARCH: '/search',
  FAVOURITE: '/favorite',
  SETTINGS: '/settings',
};

export const routes: RouteConfig[] = [
  {
    path: ROUTE_CONSTANTS.ROOT,
    component: Main,
    routes: [
      {
        path: ROUTE_CONSTANTS.SEARCH,
        component: Search,
        exact: true,
      },
      /* {
        path: ROUTE_CONSTANTS.FAVOURITE,
        component: FavoritesViewer,
        exact: true,
      }, */
      {
        path: ROUTE_CONSTANTS.SETTINGS,
        component: SettingsViewer,
        exact: true,
      },
      {
        path: ROUTE_CONSTANTS.LIST,
        component: ListPage,
        routes: [
          {
            path: '/list/section',
            component: SectionViewer,
            exact: true,
          },
          {
            path: '/list/:section/literature',
            component: LiteratureViewer,
            exact: true,
          },
          {
            path: '/list/:literature/chapter',
            component: ChapterViewer,
            exact: true,
          },
          {
            path: '/list/:chapter/kural',
            component: KuralListSwipeable,
            exact: true,
          },
          {
            path: '/list/kural/:kural',
            component: KuralDetailViewer,
            exact: true,
          },
          {
            path: '/list*',
            // eslint-disable-next-line react/display-name
            component: (): React.ReactElement => (
              <Redirect to='/list/1/kural' />
            ),
            exact: true,
          },
        ],
      },
      {
        path: ROUTE_CONSTANTS.ROOT,
        exact: true,
        component: Home,
      },
      {
        path: '/*',
        // eslint-disable-next-line react/display-name
        component: (): React.ReactElement => <Redirect to='/' />,
      },
    ],
  },
];
