import React from 'react';

import { useParams } from 'react-router-dom';
import { useCategory, useSubCategory } from 'src/lib/hooks';

import ListView from '../common/ListView';

type LiteratureViewerProps = {
  className?: string;
};

const LiteratureViewer: React.FC<LiteratureViewerProps> = (
  props: LiteratureViewerProps
) => {
  const { section } = useParams<{ section: string }>();
  const items = useSubCategory('section', Number(section), 'literature');
  const onItemSelection = useCategory('section', Number(section));
  return (
    <>
      <ListView
        className={props.className}
        items={items}
        onItemSelected={(item): void => onItemSelection(item)}
      />
    </>
  );
};

export default LiteratureViewer;
