import React from 'react';

import { useSelector } from 'react-redux';
import { useNavigation } from 'src/lib/hooks';
import { AppState } from 'src/state';

import ListView from './../common/ListView';

type SectionViewerProps = {
  className?: string;
};

const SectionViewer: React.FC<SectionViewerProps> = (
  props: SectionViewerProps
) => {
  const sections = useSelector((st: AppState) => st.kuralIndexMap.section);
  const [navigateTo] = useNavigation();
  return (
    <>
      <ListView
        className={props.className}
        items={Object.values(sections)}
        onItemSelected={(item): void => {
          navigateTo(`/list/${item.id}/literature`)();
        }}
      />
    </>
  );
};

export default SectionViewer;
