import React from 'react';

import { makeStyles, ListItem, List, Chip } from '@material-ui/core';
import clsx from 'clsx';
import { useSelector, useDispatch } from 'react-redux';
import {
  SearchCategoryLabelMap,
  SearchCategoryTypes,
  isResultOfType,
  SearchResult,
  BaseType,
} from 'src/lib/api';
import { AppState } from 'src/state/reducers';
import { setUserSelection, setAllUserSelections } from 'src/state/search';
import KuralCard from 'src/views/components/kural/KuralCard';

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(2),
    padding: theme.spacing(1),
  },
  searchWrapper: {
    textAlign: 'center',
  },
  searchFilter: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    textAlign: 'start',
    '& .search-chip': {
      height: '2em',
      margin: '0 1vw 4px 0',
      fontSize: '0.7em',
      minWidth: '15vw',
      '& svg': {
        width: '1rem',
        height: '1rem',
        [theme.breakpoints.up('sm')]: {
          width: '1.5rem',
          height: '1.5rem',
        },
      },
    },
  },
  kuralSearchItem: {
    margin: 0,
    padding: 0,
    borderBottom: 'none',
  },
}));

type SearchProps = {
  className?: string;
};

type SearchChipsProps = unknown;
type FiltersType = {
  label: string;
  type: SearchCategoryTypes;
  isShow: boolean;
};

const SearchChips: React.FC<SearchChipsProps> = (props: SearchChipsProps) => {
  const classes = useStyles();
  const [filters, setFilters] = React.useState<FiltersType[]>([]);
  const search = useSelector((st: AppState) => st.search);
  const dispatch = useDispatch();
  React.useEffect(() => {
    const hasStartsWith = (keyword: string): boolean =>
      search.filters.filter((f) => f.startsWith(keyword)).length > 0;
    const filterOptions: FiltersType[] = [
      {
        label: SearchCategoryLabelMap.all,
        type: 'all',
        isShow: new Set(search.results.map((r) => r.type)).size > 1,
      },
      {
        label: SearchCategoryLabelMap.section,
        type: 'section',
        isShow: hasStartsWith('BySection'),
      },
      {
        label: SearchCategoryLabelMap.literature,
        type: 'literature',
        isShow: hasStartsWith('ByLiterature'),
      },
      {
        label: SearchCategoryLabelMap.chapter,
        type: 'chapter',
        isShow: hasStartsWith('ByChapter'),
      },
      {
        label: SearchCategoryLabelMap.kural,
        type: 'kural',
        isShow: hasStartsWith('ByKural'),
      },
      {
        label: SearchCategoryLabelMap.detail,
        type: 'detail',
        isShow: hasStartsWith('ByKuralDetail'),
      },
    ];
    setFilters(filterOptions);
  }, [search]);
  const isSelected = (type: SearchCategoryTypes): boolean => {
    if (type === 'all') {
      return (
        search.userSelections['section'] &&
        search.userSelections['literature'] &&
        search.userSelections['chapter'] &&
        search.userSelections['kural'] &&
        search.userSelections['detail']
      );
    } else {
      return search.userSelections[type];
    }
  };

  const onClick = (type: SearchCategoryTypes): void => {
    if (type !== 'all') {
      dispatch(setUserSelection(type, !search.userSelections[type]));
    } else {
      dispatch(setAllUserSelections(!isSelected('all')));
    }
  };
  return (
    <div className={classes.searchFilter}>
      {filters
        .filter((f) => f.isShow)
        .map((f, i) => (
          <Chip
            key={i}
            size='small'
            className='search-chip'
            label={f.label}
            color={'primary'}
            variant={isSelected(f.type) ? 'default' : 'outlined'}
            onClick={(e): void => onClick(f.type)}
          />
        ))}
    </div>
  );
};

type SearchResultItemProps = {
  item: BaseType;
  label: string;
  onClick?: () => void;
};
const SearchResultItem: React.FC<SearchResultItemProps> = ({
  item,
  label,
  onClick,
}: SearchResultItemProps) => {
  return (
    <div key={item.id} onClick={onClick}>
      <div>{item.value}</div>
    </div>
  );
};

const Search: React.FC<SearchProps> = (props: SearchProps) => {
  const classes = useStyles();
  const search = useSelector((st: AppState) => st.search);

  const renderItem = (item: SearchResult): React.ReactElement | null => {
    if (isResultOfType(item, 'kural') && search.userSelections.kural) {
      return (
        <KuralCard
          key={item.item.id}
          kural={item.item}
          className={classes.kuralSearchItem}
        />
        // <SearchResultItem item={item.item} label='' onClick={(): void => {}} />
      );
    }
    // TODO:
    if (isResultOfType(item, 'section') && search.userSelections.section) {
      return <SearchResultItem item={item.item} label='' />;
    }
    if (isResultOfType(item, 'chapter') && search.userSelections.chapter) {
      return <SearchResultItem item={item.item} label='' />;
    }
    if (
      isResultOfType(item, 'literature') &&
      search.userSelections.literature
    ) {
      return <SearchResultItem item={item.item} label='' />;
    }
    // if (isResultOfType(item, 'detail') && search.userSelections.detail) {
    //   return <>detail: {item.item.value}</>;
    // }
    return null;
  };
  return (
    <div className={clsx(props.className, classes.root)}>
      <SearchChips />
      <List>
        {search.results.map((item, i) => (
          <ListItem key={i} divider={true}>
            {renderItem(item)}
          </ListItem>
        ))}
      </List>
    </div>
  );
};

export default Search;
