import React from 'react';

import { useParams } from 'react-router-dom';
import SwipeableViews from 'react-swipeable-views';
import { mod } from 'react-swipeable-views-core';
import { virtualize } from 'react-swipeable-views-utils';
import { getItemByPosition, isCategoryValid } from 'src/lib/api';
import { useNavigation } from 'src/lib/hooks';

import KuralDetail from './KuralDetail';

const VirtualizeSwipeableViews = virtualize(SwipeableViews);

type KuralDetailViewerProps = {
  className?: string;
};

const KuralDetailViewer: React.FC<KuralDetailViewerProps> = (
  props: KuralDetailViewerProps
) => {
  const [navigateTo] = useNavigation();
  const params = useParams<{ kural: string }>();
  const kuralNo = parseInt(params.kural);
  return (
    <>
      {/* <KuralDetail kural={kuralNo} /> */}
      <VirtualizeSwipeableViews
        enableMouseEvents
        overscanSlideBefore={kuralNo === 1 ? 0 : 1}
        overscanSlideAfter={kuralNo === 1330 ? 0 : 1}
        index={kuralNo - 1}
        onChangeIndex={(index, indexLatest): void => {
          navigateTo(
            `/list/kural/${getItemByPosition(
              'kural',
              index - indexLatest < 0 ? 'PREV' : 'NEXT',
              kuralNo
            )}`
          )();
        }}
        slideRenderer={({ key, index }): React.ReactNode => {
          const nextIndex = mod(index, 1330) + 1;
          // TODO: 1000 above requests firing when loading 1329th kural detail
          // return null;
          return isCategoryValid('kural', nextIndex) ? (
            <KuralDetail key={key} kural={nextIndex} />
          ) : null;
        }}
      />
    </>
  );
};

export default KuralDetailViewer;
