import React from 'react';

import { useParams } from 'react-router-dom';

import { useCategory, useSubCategory } from '../../../lib/hooks';
import ListView from '../common/ListView';

type ChapterViewerProps = {
  className?: string;
};

const ChapterViewer: React.FC<ChapterViewerProps> = (
  props: ChapterViewerProps
) => {
  const { literature } = useParams<{ literature: string }>();
  const items = useSubCategory('literature', Number(literature), 'chapter');
  const onItemSelection = useCategory('literature', Number(literature));
  return (
    <>
      <ListView
        className={props.className}
        items={items}
        onItemSelected={(item): void => onItemSelection(item)}
      />
    </>
  );
};

export default ChapterViewer;
