import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  Collapse,
  ListItemText,
  ListSubheader,
} from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import { useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { findChapterNoFromKuralNo, getKuralByIds } from 'src/lib/api';
import { Kural, BaseType } from 'src/lib/api';
import { useBaseItem } from 'src/lib/hooks';
import { useNavigation } from 'src/lib/hooks';
import { AppState } from 'src/state';

import ListView from '../common/ListView';
import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  root: {
    // marginTop: theme.spacing(2),
    // padding: theme.spacing(1),
  },
  list: {
    '& .list-group': {},
    '& .sub-list': {
      // paddingTop: 16,
      '& li:last-child': {
        borderBottom: 'unset',
        marginBottom: 'unset',
      },
    },
    '& .sub-header': {
      textAlign: 'left',
      // lineHeight: '30px',
      color: 'brown',
    },
  },
  chapFavorites: {
    paddingBottom: 0,
    '& li:last-child': {
      borderBottom: 'unset',
    },
  },
}));

type FavoritesViewerProps = {
  className?: string;
  expand?: boolean;
};

const FavoritesViewer: React.FC<FavoritesViewerProps> = (
  props: FavoritesViewerProps
) => {
  const [getChapter] = useBaseItem('chapter');
  const firestore = useFirestore();
  const {
    favorites: { kural: KURAL_IDS, chapter: CHAPTER_IDS },
    preference,
  } = useSelector((st: AppState) => st.preferences);
  const [kurals, setKurals] = React.useState<Kural[]>([]);
  const [isKuralVisible, setKuralVisible] = React.useState<boolean>(
    props.expand ?? true
  );
  const [isChapterVisible, setChapterVisible] = React.useState<boolean>(
    props.expand ?? false
  );
  const [navigateTo] = useNavigation();
  const classes = useStyles();
  const onChapterSelected = (item: BaseType): void => {
    navigateTo(`/list/${item.id}/kural`)();
  };
  React.useEffect(() => {
    if (KURAL_IDS.length > 0) {
      getKuralByIds(firestore, KURAL_IDS, preference.language.primary).then(
        (kurals) => {
          setKurals(kurals);
        }
      );
    }
  }, [firestore, KURAL_IDS, preference.language.primary]);
  const chapterKuralMap = new Map<number, BaseType[]>([]);
  kurals.forEach((it) => {
    const chap = findChapterNoFromKuralNo(it.id);
    !chapterKuralMap.has(chap)
      ? chapterKuralMap.set(chap, [it])
      : chapterKuralMap.set(chap, [...(chapterKuralMap.get(chap) || []), it]);
  });
  const noRecordsFound = <h1>None Found</h1>;
  return (
    <div className={clsx(props.className, classes.root)}>
      <List className={classes.list} disablePadding>
        <ListItem
          selected={isKuralVisible}
          className={'list-group'}
          button
          onClick={(): void => {
            setKuralVisible(!isKuralVisible);
          }}>
          <ListItemText primary='Kurals' />
          {isKuralVisible ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={isKuralVisible} timeout='auto'>
          {chapterKuralMap.size > 0 &&
            Array.from(chapterKuralMap.keys()).map((k) => {
              const chapterInfo = getChapter(k);
              return (
                <List
                  component='div'
                  className='sub-list'
                  key={k}
                  disablePadding>
                  {chapterInfo && (
                    <ListSubheader
                      className='sub-header'
                      disableSticky
                      onClick={(): void => onChapterSelected(chapterInfo)}>
                      {chapterInfo.value}
                    </ListSubheader>
                  )}

                  {chapterKuralMap.get(k)?.map((item) => (
                    <KuralCard key={item.id} kural={item} />
                  ))}
                </List>
              );
            })}
          {chapterKuralMap.size === 0 && noRecordsFound}
        </Collapse>
        <ListItem
          selected={isChapterVisible}
          className={'list-group'}
          button
          onClick={(): void => {
            setChapterVisible(!isChapterVisible);
          }}>
          <ListItemText primary='Chapters' />
          {isChapterVisible ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={isChapterVisible} timeout='auto'>
          {CHAPTER_IDS.length > 0 && (
            <ListView
              className={classes.chapFavorites}
              items={
                CHAPTER_IDS.map((ch) => getChapter(ch)).filter(
                  (ch) => ch !== null
                ) as BaseType[]
              }
              onItemSelected={(item: BaseType): void => onChapterSelected(item)}
            />
          )}
          {CHAPTER_IDS.length === 0 && noRecordsFound}
        </Collapse>
      </List>
    </div>
  );
};

export default FavoritesViewer;
